@mixin reset-list() {
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;

  list-style: none;
}

@mixin make-center() {
  margin-left: auto;
  margin-right: auto;
}

@mixin init-list() {
  @include reset-list();
  @include make-center();
}

@mixin make-shadow() {
  .accordeon__item-top--level1 {
    border-bottom-color: $green3;
  }
}
